.form {
    background-color: white;
    padding: 25px;
    margin: 0 0 7px 0;
    display: grid;
    grid-template-columns: 1fr auto;
    grid-gap: 20px;
}

@media(max-width: 767px) {
    .form {
        grid-template-columns: 1fr;
    }
}

.form__input {
    padding: 8px;
}

.form__button {
    padding: 8px;
    background-color: hsl(180, 100%, 25%);
    color: white;
    border: 0;
    transition: 0.5s;
    cursor: pointer;
}

.form__button:hover {
    background-color: hsl(180, 100%, 30%);
    transform: scale(1.1);
}

@media(max-width: 767px) {
    .form__button:hover {
        transform: scale(1.02);
    }
}

.form__button:active {
    background-color: hsl(180, 100%, 35%);
}